@import url(https://fonts.googleapis.com/css?family=Poppins:400,500,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.link-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  text-decoration: underline;
  display: inline;
  margin: 0;
  padding: 0;
}

.link-button:hover,
.link-button:focus {
text-decoration: none;
}

html, body {
  height: 100%;
  width: 100%;
  font-family: 'Poppins', sans-serif;
  color: #222;
}
#name {
  font-size: 40px;
}
.navbar {
  padding: .8rem;
}
.navbar-nav {
  padding-right: 10px;
}
.nav-link {
  font-size: 1.1em !important;
}
.carousel-inner img{
  width: 100%;
  height: 100%;
}
.carousel-caption {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.carousel-caption h1 {
  font-size: 500%;
  text-shadow: 1px 1px 15px #000;
}
.padding {
  padding-bottom: 2rem;
}
.welcome, .contact {
  width: 75%;
  margin: 0 auto;
  padding-top: 2rem;
}
.welcome hr, .contact hr {
  border-top: 2px solid #b4b4b4;
  width: 95%;
  margin-top: .3rem;
  margin-bottom: 1rem;
}
.fa-code {
  color: #e44d26;
}
.fa-bold {
  color: #563d7c;
}
.fa-css3, .fa-database {
  color: #264de4;
}
.fa-js {
  color: #f0db4f;
}
.fa-node-js {
  color: #3c873a;
}
.fa-react {
  color: #61dbfb;
}
.fa-code,.fa-bold,.fa-css3,.fa-js,.fa-node-js,.fa-react,.fa-database {
  font-size: 4em;
}
.c-sharp,.nosql {
  height: 4em;
  width: 4em;
}
.fun {
  width: 100%;
  margin-bottom: 2rem;
}
.gif {
  max-width: 100%;
}
.social a {
  font-size: 4.5em;
  padding: 3rem;
}
.fa-linkedin {
  color: #2867b2
}
.fa-github {
  color: #000000;
}
.fa-envelope {
  color: #d44638;
}
.fa-linkedin:hover {
  color: #0192e0;
}
.fa-github:hover {
  color: #2f4f4f;
}
.fa-envelope:hover {
  color: #b23121;
}
#fixed {
  color: #989999;
}
footer {
  background-color: #3f3f3f;
  color: #d5d5d5;
  padding-top: 2rem;
}
footer a {
  color: #d5d5d5;
}
footer h4, footer a:hover{
  color: #f5f5f5;
}
hr.light {
  border-top: 1px solid #d5d5d5;
  width: 75%;
  margin-top: 0.8rem;
  margin-bottom: 1rem;
}
hr.w100{
  width: 100%;
}

/*---Media Queries --*/
@media (max-width: 992px) {
  .social a {
    font-size: 4rem;
    padding: 2rem;
  }
}
@media (max-width: 768px) {
  .carousel-caption {
    top: 45%;
  }
  .carousel-caption h1 {
    font-size: 350%;
  }
  .display-4 {
    font-size: 200%;
  }
  .social a {
    font-size: 2.5rem;
    padding: 1.2rem;
  }
}
@media (max-width: 576px) {
  #name {
    font-size: 32px;
  }
  .carousel-caption {
    top: 40%;
  }
  .carousel-caption h1 {
    font-size: 250%;
  }
  .carousel-indicators {
    display: none;
  }
  .display-4 {
    font-size: 160%;
  }
  .social a {
    font-size: 2rem;
    padding: 0.7rem;
  }
  .padding {
    padding-bottom: 1.2rem;
  }
}


/*---Firefox Bug Fix --*/
.carousel-item {
  transition: transform 0.5s ease;
  -webkit-backface-visibility: visible;
  backface-visibility: visible;
}
/*--- Fixed Background Image --*/
figure {
  position: relative;
  width: 100%;
  height: 40%;
  margin: 0!important;
}
.fixed-wrap {
  clip: rect(0, auto, auto, 0);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
#fixed {
  background-image: url(/static/media/mac.f21dceb6.png);
  position: fixed;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center center;
  transform: translateZ(0);
  will-change: transform;
}
/*--- Bootstrap Padding Fix --*/
[class*="col-"] {
    padding: 1rem;
}


/*--- Icon resizing --*/
.myIcon {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
